<template>
  <v-container fluid class="info-container">

    <v-sheet
        class="current-time"
        elevation="19"
    >
      <div>
        <v-icon class="icon-font">mdi-clock-time-two-outline</v-icon>
        <div>Текущее время:</div>
        <div>{{ currentTime }}</div>
      </div>
    </v-sheet>

  </v-container>
</template>

<script>
import { formattedDate } from '../utils/dateTime'

export default {
  data: function () {
    return {
      currentTime: ''
    }
  },
  created() {
    setInterval(this.getCurrentTime, 1000)
  },
  methods: {
    getCurrentTime: function () {
      this.currentTime = formattedDate(new Date())
    }
  },
  computed: {}
}
</script>

<style scoped>
.current-time {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  border-radius: 30px;
  min-width: 300px;
  height: 250px;
  margin-top: 30px;
  font-size: 1.5rem;

}

.info-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-font {
  color: #2e2a3f;
  font-size: 6rem;
  margin-bottom: 1rem;
}
</style>
